.benefits {
  &__items {
    display: flex;
    align-items: end;
    justify-content: space-around;
    position: relative;
    min-height: 654px;
  }

  &__image {
    position: relative;
    left: -14px;
    bottom: -10px;
  }

  &__item {
    position: absolute;
  }

  &__item1 {
    left: 0px;
    bottom: 4px;
  }

  &__item2 {
    left: 22px;
    bottom: 44%;
  }

  &__item3 {
    left: 36%;
    top: 10px;
  }

  &__item4 {
    right: 34px;
    bottom: 31%;
  }

  &__item5 {
    right: 30px;
    bottom: 6px;
  }

  &__name {
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: var(--dark-color);
    margin-bottom: 16px;
    span {
      font-weight: 700;
      display: none;
      width: 100%;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);

    & li:not(:last-child) {
      margin-bottom: 4px;
    }

    & p:not(:last-child) {
      margin-bottom: 16px;
    }
  }
}
