.production_block {
  background: var(--grey-bg-color);

  &__title {
    margin-bottom: 30px;
  }

  &__item {
    padding: 62px 73px 30px 73px;
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
    border-radius: 4px;
    background-color: var(--light-color);
    text-align: center;
  }

  &__name {
    text-align: center;
    margin-bottom: 16px;
    margin-top: 40px;
  }

  &__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: var(--text-color);
    margin-bottom: 32px;
  }

  &__button {
    font-size: 16px;
    line-height: 24px;
    padding: 12px 24px;
  }
}
