.service {
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      color: var(--text-color);

      &:last-child {
        color: var(--grey-date-color);
      }
    }
  }

  &__inner {
    @include flex-all-sb;
    margin-top: 56px;
    margin-bottom: 30px;
  }

  &__title {}

  &__block {
    padding-top: 60px;
    padding-right: 60px;
    padding-bottom: 60px;
    background: var(--light-color);
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
    display: flex;
    justify-content: space-between;
  }

  &__left {
    max-width: 468px;
  }

  &__navigation {
    display: flex;
    column-gap: 78px;
    max-width: 248px;
    margin: 0 auto;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__arrow {
    cursor: pointer;
  }

  &__right {
    max-width: 580px;
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 60px;
  }

  &__items {
    @include grid-2-fr;
    grid-row-gap: 60px;
    margin-bottom: 60px;
  }

  &__item {
    display: flex;
    column-gap: 16px;
  }

  &__check {
    width: 24px;
    height: 24px;
  }

  &__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__subname {
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: var(--dark-color);
  }

  &__buttons {
    display: flex;
    column-gap: 24px;
  }
}
