.block_show930 {
  display: none;
}

@media screen and (max-width: 1440px) {}

@media screen and (max-width: 1200px) {

  //hero

  .hero__content {
    left: 0px;
  }

  //benefits

  .benefits__image {
    width: 380px;
    height: 311px;
    left: -50px;
  }

  .benefits__items {
    min-height: 617px;
  }

  .benefits__name {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 8px;
  }

  .benefits__item1 {
    max-width: 220px;
  }

  .benefits__item2 {
    left: 0;

    .benefits__name {
      max-width: 163px;
    }
  }

  .benefits__item3 {
    max-width: 300px;

    .benefits__name {
      max-width: 300px;
    }
  }

  .benefits__item4 {
    right: 0;
    max-width: 300px;
  }

  .benefits__item5 {
    right: 0;
  }

  //footer

  .footer__location-text {
    max-width: 200px;
  }

  //service

  .service__button_left {
    padding: 19px 32px;
  }

  .service__button_right {
    padding: 19px 32px;
  }

}

@media screen and (max-width: 1090px) {

  //header

  .head_container .nav__item:not(:last-child) {
    margin-right: 24px;
  }

  .head_container .nav__link {
    font-size: 16px;
    line-height: 24px;
  }

  .nav__item:not(:last-child) {
    margin-right: 24px;
  }

  .nav__link {
    font-size: 16px;
    line-height: 24px;
  }

  //service

  .service__left {
    max-width: 380px;
  }

}

@media screen and (max-width: 992px) {

  //global

  .section {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  //header

  .header {
    padding-top: 16px;
    padding-bottom: 16px;
  }

  //hero

  .hero {
    height: 700px;
    position: relative;
  }

  .hero__slide {
    padding-top: 177px;
    padding-bottom: 220px;
  }

  .hero__title {
    font-size: 48px;
    line-height: 60px;
    max-width: 448px;
  }

  //content_video

  .content_video__image {
    max-height: 500px;
  }

  //principles

  .principles__item {
    padding: 20px;
  }

  .principles__name {
    margin-bottom: 4px;
    font-size: 20px;
    line-height: 28px;
  }

  //footer

  .footer__links {
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 20px;
  }

  //production_block

  .production_block__item {
    padding: 10px;
    padding-bottom: 30px;
  }

  .production_block__img {
    height: 428px;
  }

  .news_block .block_preview {
    margin-bottom: 80px;
  }

  //products

  .products__items {
    grid-column-gap: 20px;
    margin-bottom: 80px;
  }

  .products {
    padding-bottom: 80px;
  }

  //service
  .service {
    padding-bottom: 80px;
  }

  .service__block {
    padding-top: 20px;
    padding-right: 20px;
    padding-bottom: 49px;
  }

  .service__image {
    width: 378px;
  }

  //documentation

  .documentation__items {
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 20px;
  }

  .documentation__item {
    height: 112px;
  }

  //contact_us

  .contact_us__container {
    column-gap: 66px;
  }

  .contact_us__title {
    max-width: 388px;
  }
}

@media screen and (max-width: 930px) {

  //header

  .burger {
    display: block;
  }

  .header .nav {
    display: none;
  }

  .header__button {
    display: none;
  }

  .head_container .burger {
    color: var(--light-color);
  }

  //benefits

  .benefits__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
    align-items: start;
  }

  .benefits__item {
    position: static;
    max-width: 100%;
  }

  .benefits__text {
    max-width: 100%;
  }

  .benefits__image {
    grid-column-start: 1;
    grid-column-end: 3;
    position: static;
    display: flex;
    margin: 0 auto;
  }

  //footer

  .footer__info {
    flex-wrap: wrap;
    column-gap: 41px;
    row-gap: 20px;
  }

  .footer .nav {
    display: none;
  }

  .footer__info {
    padding-top: 0;
  }

  //service

  .service__title {
    font-size: 36px;
    line-height: 44px;
  }

  .service__block {
    flex-direction: column;
  }

  .service__right {
    max-width: 100%;
    padding-left: 16px;
  }

  .documentation__items {
    grid-template-columns: repeat(2, 1fr);
  }

  .service__inner {
    flex-direction: column;
    align-items: start;
    row-gap: 16px;
  }

  //contact_us

  .contact_us__container {
    flex-wrap: wrap;
    row-gap: 20px;
  }

  .contact_us__title {
    max-width: 100%;
  }


}

@media screen and (max-width: 768px) {
  //principles

  .principles__items {
    grid-template-columns: repeat(1, 1fr);
  }

  //footer

  .footer__links {
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 20px;
  }

  //news_block

  .news_block__items {
    grid-template-columns: repeat(2, 1fr);
  }

  .news_block__image_preview {
    width: 100%;
  }

}

@media screen and (max-width: 650px) {

  //products

  .products__items {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 576px) {
  //global

  .section {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .h2 {
    font-size: 30px;
    line-height: 36px;
  }

  .h3 {
    font-size: 24px;
    line-height: 32px;
  }

  //header
  .head_container .header {
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .header__image {
    width: 52.93px;
    height: 64px;
  }

  .header {
    padding-top: 8px;
    padding-bottom: 8px;
  }

  //hero
  .hero {
    height: 100%;
  }

  .hero__slide {
    padding-top: 140px;
    padding-bottom: 172px;
  }

  .hero__title {
    font-size: 30px;
    line-height: 36px;
    margin-bottom: 32px;
  }

  .hero__button {
    font-size: 16px;
    line-height: 24px;
    padding: 12px 24px;
    padding-right: 56px;
  }

  //content_video

  .content_video__title {
    margin-bottom: 16px;
  }

  .content_video__image {
    height: 200px;
  }

  //principles

  .principles__title {
    margin-bottom: 24px;
  }

  .principles__items {
    grid-row-gap: 8px;
  }

  .principles__item {
    padding: 16px;
    column-gap: 16px;
    align-items: start;
  }

  .principles__image {
    width: 48px;
    height: 48px;
  }

  .principles__name {
    font-size: 18px;
    line-height: 26px;
  }

  //benefits

  .benefits__title {
    margin-bottom: 24px;
  }

  .benefits__items {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 24px;
  }

  .benefits__image {
    grid-column-start: inherit;
    grid-column-end: inherit;
    width: 288.24px;
    height: 100%;
  }

  .benefits__name span {
    display: block;
  }

  //footer

  .footer {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .footer__info {
    row-gap: 16px;
  }

  .footer__info .logo,
  .footer__location,
  .footer__mail,
  .footer__button,
  .footer__items {
    width: 100%;
    justify-content: start;
  }

  .footer__button {
    width: auto;
  }

  .footer__links {
    padding-top: 24px;
    flex-direction: column;
    align-items: start;
    row-gap: 8px;
  }

  //hero_company

  .hero_company__title {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .hero_company__info {
    flex-direction: column;
    align-items: start;
    row-gap: 16px;
  }

  .hero_company__description {
    padding-left: 0;
  }

  .hero_company__img {
    width: 80px;
    height: 80px;
  }

  .hero_company__description {
    font-size: 16px;
    line-height: 24px;
  }

  //production_block

  .production_block__title {
    margin-bottom: 16px;
  }

  .production_block__item {
    padding: 16px 10px 24px 10px;
  }

  .production_block__img {
    height: 184px;
  }

  .production_block__name {
    margin-top: 16px;
    margin-bottom: 16px;
    font-size: 20px;
    line-height: 28px;
  }

  .production_block__description {
    margin-bottom: 24px;
  }

  //news_block

  .news_block .block_preview {
    height: 400px;
    padding-left: 16px;
    padding-bottom: 16px;
    margin-bottom: 40px;
  }

  .block_preview__subname {
    font-size: 20px;
    line-height: 28px;
  }

  .block_preview__text {
    font-size: 14px;
    line-height: 20px;
  }

  .news_block__title {
    margin-bottom: 16px;
  }

  //slider show

  .block_show930 {
    display: block;
  }

  .block_hide930 {
    display: none;
  }

  .news_block__name {
    margin-bottom: 8px;
  }

  .news_block__name {
    font-size: 18px;
    line-height: 26px;
  }

  //products

  .products__title {
    margin-top: 16px;
    margin-bottom: 24px;
  }

  .products__items {
    margin-bottom: 40px;
    grid-row-gap: 24px;
  }

  .products__item {
    min-height: 516px;
  }

  .products__inner {
    padding: 16px;
  }

  .products__name {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 16px;
  }

  .products__text {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 24px;
  }

  .products__descripruion {
    font-size: 14px;
    line-height: 20px;
  }

  .products .item_open {
    font-size: 14px;
    line-height: 20px;
  }

  .products {
    padding-bottom: 40px;
  }

  //service

  .service {
    padding-bottom: 40px;
  }

  .service__inner {
    margin-top: 24px;
    margin-bottom: 24px;
  }

  .service__title {
    font-size: 24px;
    line-height: 32px;
  }

  .service__image {
    width: 194px;
    display: flex;
    margin: 0 auto;
  }

  .service__left {
    max-width: 100%;
  }

  .Swipersingle .swiper-pagination {
    position: static;
  }

  .Swipersingle .swiper-button-next,
  .Swipersingle .swiper-button-prev {
    bottom: -14px;
  }

  .service__text {
    margin-top: 38px;
  }

  .service__items {
    grid-template-columns: repeat(1, 1fr);
    row-gap: 16px;
    margin-bottom: 24px;
  }

  .service__name {
    font-size: 12px;
    line-height: 16px;
  }

  .service__subname {
    font-size: 20px;
    line-height: 28px;
  }

  .service__buttons {
    flex-direction: column;
    row-gap: 16px;
  }

  .service__button_left {
    font-size: 14px;
    line-height: 20px;
    padding: 14px auto;
  }

  .service__button_right {
    font-size: 14px;
    line-height: 20px;
    padding: 14px auto;
  }

  .service__block {
    padding: 16px;
    padding-top: 10px;
  }

  .service__right {
    padding-left: 0;
  }

  //documentation

  .documentation__title {
    margin-bottom: 16px;
  }

  .documentation__items {
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 10px;
  }

  .documentation__item {
    column-gap: 14px;
    padding: 32px 16px;
    padding-left: 22px;
  }

  //table

  .table__reverse .table__title {
    font-size: 12px;
    line-height: 16px;
  }

  .table__items {
    margin-bottom: 16px;
  }

  .table__item {
    padding: 16px 8px;
    column-gap: inherit;
  }

  .table__button {
    padding: 12px auto;
    font-size: 16px;
    line-height: 24px;
    width: 100%;
  }

  .table__reverse .table__count {
    font-size: 12px;
    line-height: 16px;
  }

  .table__reverse .table__units {
    font-size: 12px;
    line-height: 16px;
  }

  //contact_us

  .contact_us {
    padding-top: 45px;
    padding-bottom: 60px;
  }

  .contact_us__title {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 24px;
  }

  .contact_us__container {
    flex-direction: column;
    row-gap: 0px;
    text-align: center;
  }

  .contact_us__button {
    padding: 14px 32px;
    padding-right: 64px;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 16px;
  }
}

@media screen and (max-width: 500px) {
  .Swipersingle .swiper-button-prev {
    left: 24px;
  }

  .Swipersingle .swiper-button-next {
    right: 24px;
  }
}

@media screen and (max-width: 375px) {}
