.hero_company {
  height: 500px;
  @include bg-cover;

  &__title {
    margin-top: 96px;
    color: var(--light-color);
    margin-bottom: 48px;
  }

  &__info {
    @include flex-v-center;
  }

  &__description {
    font-weight: 600;
    font-size: 24px;
    line-height: 32px;
    max-width: 798px;
    color: var(--light-color);
    padding-left: 40px;
  }
}
