.head_container {
  .button__empty {
    padding: 10px 24px;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: var(--light-color);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 55px;
    background-color: transparent;
    cursor: pointer;

    &:hover {
      background-color: var(--red-color);
      border: 1px solid var(--red-color);
    }
  }
}

.footer .button__empty {
  color: var(--light-color);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.button__empty {
  padding: 10px 24px;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--dark-color);
  border: 1px solid var(--red-color);
  border-radius: 55px;
  background-color: transparent;
  cursor: pointer;

  &:hover {
    background-color: var(--red-color);
    border: 1px solid var(--red-color);
    color: var(--light-color);
  }
}

.button {
  display: inline-block;
  text-align: center;
  justify-content: center;
  background-color: var(--red-color);
  color: var(--light-color);
  border: 1px solid var(--red-color);
  border-radius: 4px;
  padding: 19px 48px;
  cursor: pointer;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;

  &:hover {
    background-color: var(--red-h-color);
  }
}


.button2 {
  display: inline-block;
  text-align: center;
  justify-content: center;
  background-color: var(--light-color);
  color: var(--dark-color);
  border: 1px solid var(--red-color);
  border-radius: 4px;
  padding: 19px 48px;
  cursor: pointer;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;

  &:hover {
    background-color: var(--red-color);
    color: var(--light-color);
  }
}

.hero__button {
  padding-right: 80px;
}

.button3 {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: var(--grey-date-color);
  padding: 10px 24px;
  padding-left: 36px;
  border: 1px solid rgba(123, 109, 113, 0.2);
  border-radius: 4px;
  position: relative;
  max-height: 40px;

  &:before {
    background-image: url(../img/svg/arrow_left_grey.svg);
    position: absolute;
    left: 19px;
    top: 50%;
    transform: translateY(-50%);
    content: '';
    width: 11px;
    height: 8px;
  }

  &:hover {
    color: var(--text-color);
    border: 1px solid var(--red-color);
    border-radius: 4px;
  }
}
