.products {
  background: var(--grey-bg-color);
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      color: var(--text-color);

      &:last-child {
        color: var(--grey-date-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 24px;
  }

  &__items {
    @include grid-2-fr;
    margin-bottom: 100px;
  }

  &__item {
    background: var(--light-color);
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
    border-radius: 4px;
    min-height: 604px;
    display: flex;
    flex-direction: column;
  }

  &__inner {
    padding: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
    flex-grow: 1;
  }

  &__name {
    margin-bottom: 16px;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
    margin-bottom: 32px;
  }

  &__button {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    padding: 12px 24px;
    margin-top: auto;
  }

  &__descripruion {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
  }

  .item_open {
    background-image: url(../img/svg/arrow_down.svg);
    background-repeat: no-repeat;
    background-position: 140px center;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    color: var(--text-color);
    cursor: pointer;
    margin-top: 16px;
  }

  .item_close {
    max-height: 220px;
    transition: max-height 0.2s ease-out;
    overflow: hidden;
  }

  .open2 {
    background-image: url(../img/svg/arrow_up.svg);
  }
}
