.plus {
  &__item {
    @include mb(16px);
    padding-left: 40px;
    position: relative;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--text-color);

    &::before {
      position: absolute;
      content: '';
      width: 24px;
      height: 24px;
      top: 4px;
      left: 0;
      background-image: url(../img/svg/check.svg)
    }
  }

  &__list {}

  &__item2 {
    padding-left: 60px;
    position: relative;
    margin-bottom: 16px;
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);

    &::before {
      position: absolute;
      content: '';
      width: 4px;
      height: 4px;
      top: 12px;
      left: 40px;
      border-radius: 50%;
      background: var(--red-color);
    }
  }
}

.plus1 {
  &__container {
    max-width: 970px;
    margin: 0 auto;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__subtitle {
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: var(--dark-color);
    margin-bottom: 30px;
    max-width: 970px;
  }

  .plus__item {
    max-width: 970px;
  }
}

.plus2 {
  background: var(--grey-bg-color);

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__inner {
    max-width: 640px;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__subtitle {
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: var(--dark-color);
  }
}

.plus3 {
  background-color: var(--light-color);

  .plus2__container {
    margin-bottom: 100px;
  }

  &__care {
    background-color: var(--red-color);
    padding: 60px 100px;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--light-color);
  }
}

.plus4 {
  background-color: var(--grey-bg-color);
}

.plus5 {
  &__container {
    @include flex-all-sb;
  }

  &__text {
    max-width: 640px;

    p {
      font-weight: 400;
      font-size: 18px;
      line-height: 26px;
      color: var(--text-color);
      @include mb(20px);
    }
  }
}

.plus6 {
  background-color: var(--grey-bg-color);

  &__block {
    padding: 100px;
    background-color: var(--light-color);

    p {
      font-weight: 400;
      font-size: 18px;
      line-height: 26px;
      color: var(--text-color);
      @include mb(20px);
    }
  }
}

.plus7 {
  padding-top: 60px;
  padding-bottom: 160px;
}
