.accordion {
  &__item {
    background-color: var(--light-color);
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
    border-radius: 4px;
    max-width: 840px;
    @include mb(30px);
  }

  &__header {
    padding: 30px;
    padding-left: 78px;
    cursor: pointer;
    position: relative;
    min-height: 92px;

    &:after {
      content: '';
      position: absolute;
      left: 35px;
      top: 38px;
      width: 14px;
      height: 14px;

      background-image: url(../img/svg/plus.svg);
    }
  }

  &__body {
    padding-left: 78px;
    padding-bottom: 30px;
    padding-right: 30px;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
    margin-bottom: 24px;

    p {
      @include mb(16px);
    }
  }

  &__title {
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
    /* identical to box height, or 144% */


    /* 231F20 */

    color: var(--dark-color);
    margin-bottom: 16px;
  }

  &__list {
    margin-bottom: 24px;
  }

  &__li {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
    @include mb(12px);
    position: relative;
    padding-left: 20px;

    &::before {
      position: absolute;
      content: '';
      width: 4px;
      height: 4px;
      border-radius: 50%;

      left: 0;
      top: 10px;

      background: var(--red-color);
    }
  }

  &__ol {
    counter-reset: li;
  }

  &__num {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
    @include mb(12px);
    position: relative;
    padding-left: 40px;

    &:before {
      counter-increment: li;
      content: counters(li, ".") ". ";
      position: absolute;
      left: 0;
      top: 0px;
    }
  }

  &__item_show .accordion__header {
    &:after {
      background-image: url(../img/svg/minus.svg);
      width: 14px;
      height: 2px;
      top: 44px;
    }
  }

  &__item_slidedown .accordion__header {
    &:after {
      background-image: url(../img/svg/minus.svg);
      width: 14px;
      height: 2px;
      top: 44px;
    }
  }

  .accordion__item:not(.accordion__item_show) .accordion__body {
    display: none;
  }
}
