.mySwiper {
  .container {
    position: relative;
  }

  .swiper-button-next,
  .swiper-button-prev {
    top: inherit;
    bottom: 48px;
  }



  .swiper-pagination-fraction {
    bottom: 58px;
    left: 134px;
    width: auto;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
  }

  .swiper-button-prev {
    width: 44px;
    height: 44px;
    left: 26px;

    &::after {
      background-image: url(../img/svg/arrow_left_white.svg);
      position: absolute;
      content: "";
      width: 16px;
      height: 12px;
    }

    &:hover::after {
      background-image: url(../img/svg/arrow_left_red.svg);
    }

    &::before {
      content: "";
      position: absolute;
      width: 44px;
      height: 44px;
      border: 1px solid rgba(255, 255, 255, 0.3);
      border-radius: 4px;
    }

    &:hover::before {
      background-color: var(--light-color);
    }
  }

  .swiper-button-next {
    width: 44px;
    height: 44px;
    right: auto;
    left: 226px;

    &::after {
      background-image: url(../img/svg/arrow_right_white.svg);
      position: absolute;
      content: "";
      width: 16px;
      height: 12px;
    }

    &:hover::after {
      background-image: url(../img/svg/arrow_right_red.svg);
    }

    &::before {
      content: "";
      position: absolute;
      width: 44px;
      height: 44px;
      border: 1px solid rgba(255, 255, 255, 0.3);
      border-radius: 4px;
    }

    &:hover::before {
      background-color: var(--light-color);
    }
  }
}

.Swipersingle {

  .swiper-button-next,
  .swiper-button-prev {
    top: inherit;
    bottom: -4px;
    z-index: 999;
  }

  .swiper-button-prev {
    left: 125px;

    &::after {
      background-image: url(../img/svg/arrow_left_dark.svg);
      position: absolute;
      content: "";
      width: 16px;
      height: 12px;
    }
  }

  .swiper-button-next {
    right: 125px;
    left: auto;

    &::after {
      background-image: url(../img/svg/arrow_right_dark.svg);
      position: absolute;
      content: "";
      width: 16px;
      height: 12px;
    }
  }
}

.swipernews {
  .swiper-pagination-bullet {
    background-color: var(--grey-doc-color);
    @include mr(40px);
    width: 8px;
    height: 8px;
  }

  .swiper-pagination-bullet-active {
    background-color: var(--red-color)
  }

  .swiper-pagination {
    position: static;
  }

  .news_block__item {
    min-height: 366px;
    margin-bottom: 26px;
  }
}
