.documentation {
  background: var(--grey-doc-color);

  &__title {
    margin-bottom: 30px;
  }

  &__items {
    @include grid-4-fr;
  }

  &__item {
    padding: 10px 26px;
    display: flex;
    column-gap: 22px;
    align-items: center;
    background: var(--light-color);
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
    border-radius: 4px;
    height: 120px;
  }

  &__name {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-decoration-line: underline;
    color: var(--dark-color);
  }
}
