.policy {
  padding-bottom: 100px;

  .breadcrumbs {
    margin-bottom: 46px;

    p {
      color: var(--text-color);

      &:last-child {
        color: var(--grey-date-color);
      }
    }
  }

  &__container {
    display: flex;
    justify-content: space-between;
  }
  &__content {
    max-width: 970px;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__name {
    margin-bottom: 20px;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
    @include mb(48px);
  }
}
