.block_preview {
  width: 100%;
  height: 570px;
  @include bg-cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-left: 40px;
  padding-bottom: 40px;

  &__subname {
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: var(--light-color);
    margin-bottom: 16px;
  }

  &__text {
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
    margin-bottom: 32px;
  }

  .button {
    max-width: 134px;
    padding: 12px 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
  }
}
