.news {
  background-color: var(--grey-bg-color);
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      color: var(--text-color);

      &:last-child {
        color: var(--grey-date-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__group {
    margin-bottom: 40px;
  }

  &__block {
    background-color: var(--light-color);
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
    border-radius: 4px;
    padding: 30px;
    display: flex;
    column-gap: 30px;
    @include mb(30px);
  }

  &__info {
    max-width: 810px;
    display: flex;
    flex-direction: column;
    align-items: start;
    flex-grow: 1;
  }

  &__date {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--grey-date-color);
    margin-bottom: 16px;
  }

  &__name {
    margin-bottom: 16px;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
    margin-bottom: 46px;
  }

  &__button {
    padding: 10px 24px;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
    margin-top: auto;
  }

  &__pagination {
    @include flex-all-center;
    column-gap: 30px;

    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      text-align: center;
      color: var(--dark-color);
      cursor: pointer;
    }
  }

  &__left {
    margin-right: 4px;
    cursor: pointer;
  }

  &__right {
    margin-left: 4px;
    cursor: pointer;
  }

  .pagin_center {
    width: 30px;
    height: 30px;
    background-color: var(--red-color);
    border-radius: 4px;
    padding: 3px 10px;
    color: var(--light-color)
  }
}
