.principles {
  background-color: var(--grey-color);
  &__title {
    max-width: 515px;
  }

  &__items {
    @include grid-2-fr;
  }

  &__item {
    @include flex-v-center;
    column-gap: 20px;
    border: 2px solid var(--red-color);
    border-radius: 4px;
    padding: 40px;
    background-color: var(--light-color);
  }

  &__name {
    margin-bottom: 8px;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
  }
}
