.breadcrumbs {
  display: flex;
  column-gap: 10px;
  padding-top: 16px;

  p {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-decoration-line: underline;
    color: var(--light-color);
    cursor: pointer;

    &:last-child {
      color: rgba(255, 255, 255, 0.5);
      text-decoration-line: none;
      cursor: text;
    }
  }

  img {
    object-fit: none;
  }
}
