.footer {
  background: linear-gradient(86.15deg, #231F20 0%, #40393B 100%);
  padding-top: 80px;
  padding-bottom: 80px;

  .nav {
    padding-bottom: 48px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);

  }

  .nav__list {
    @include flex-all-sb;
  }

  &__info {
    padding-top: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    @include flex-all-sb;
  }

  &__location {
    @include flex-all-sb;
    column-gap: 8px;
  }

  &__location-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--light-color);
  }

  &__mail {
    @include flex-all-sb;
    column-gap: 10px;
  }

  &__mail-link {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--light-color);
  }

  &__items {
    display: flex;
    column-gap: 8px;
  }

  &__social {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }

  &__social_tg {
    background-image: url(../img/svg/tg_empty.svg);

    &:hover {
      background-image: url(../img/svg/tg_full.svg);
    }
  }
  &__social_vk {
    background-image: url(../img/svg/vk_empty.svg);

    &:hover {
      background-image: url(../img/svg/vk_full.svg);
    }
  }
  &__social_insta {
    background-image: url(../img/svg/insta_empty.svg);

    &:hover {
      background-image: url(../img/svg/insta_full.svg);
    }
  }
  &__social_yt {
    background-image: url(../img/svg/youtube_empty.svg);

    &:hover {
      background-image: url(../img/svg/youtube_full.svg);
    }
  }

  &__links {
    @include flex-all-sb;
    padding-top: 48px;
  }

  &__text {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: var(--light-color);
    text-decoration: uppercase;
  }

  &__link {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: var(--light-color);
    text-decoration-line: underline;
  }

}
