.documents {
  padding-bottom: 60px;

  .breadcrumbs {
    p {
      color: var(--text-color);

      &:last-child {
        color: var(--grey-date-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__items {
    @include flex-all-sb;
    column-gap: 30px;
  }

  &__item {
    @include flex-v-center;
    column-gap: 16px;
    background-color: var(--red-color);
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
    border-radius: 4px;
    padding: 60px;
    width: 570px;
  }

  &__name {
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
    color: var(--light-color);

  }
}

.certificate {
  background-color: var(--grey-bg-color);
  padding-top: 60px;
  padding-bottom: 100px;

  &__links {
    @include flex-v-center;
    column-gap: 30px;
    margin-bottom: 60px;
  }

  &__link {
    padding: 11px 48px;
    background-color: var(--light-color);
    border-radius: 4px;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    color: var(--red-color);

    &:hover {
      background-color: var(--red-color);
      color: var(--light-color)
    }
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: var(--black-color);
    margin-bottom: 30px;
  }

  &__photo {
    @include grid-4-fr;
    margin-bottom: 60px;
  }

  &__picture {
    text-align: center;
  }

  &__img {
    background-color: var(--light-color);
    padding: 7px 5px;
    margin-bottom: 16px;
    cursor: pointer;
  }

  &__info {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: var(--text-color);
  }

  &__items {
    @include grid-4-fr;
  }

  &__item {
    background-color: var(--light-color);
    border: 1px solid var(--grey-doc-color);
    border-radius: 4px;
    min-height: 398px;

    display: flex;
    flex-direction: column;
  }

  &__inner {
    padding: 20px 30px 30px 30px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__name {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    color: var(--dark-color);
    margin-bottom: 24px;
  }

  &__button {
    padding: 14px 45px;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: var(--red-color);
    margin-top: auto;
  }
}
