.control {
  @include bg-cover;
  text-align: center;
  padding-bottom: 173px;

  .breadcrumbs {
    justify-content: center;
  }

  &__title {
    margin-top: 138px;
    color: var(--light-color);
    margin-bottom: 48px;
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    color: var(--light-color);
    margin-bottom: 48px;
  }

  &__button {
    padding-right: 80px;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      background-image: url(../img/svg/arrow_right_white.svg);
      width: 16px;
      height: 12px;
      right: 52px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
    }
  }
}
