// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Commissioner", sans-serif;
  --content-width: 1170px;
  --container-offset: 26px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --red-color: #B12237;
  --red-h-color: #9B1E30;
  --dark-color: #231F20;
  --black-color: #000;
  --text-color: #5E5356;
  --grey-color: #F5F5F5;
  --grey-bg-color: #F0F0F0;
  --grey-date-color: #7B6D71;
  --grey-doc-color: #E6E6E6;
  --grey-border-color: #EFF1F6;
}
