.ask {
  background-color: var(--grey-bg-color);
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      color: var(--text-color);

      &:last-child {
        color: var(--grey-date-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 30px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__aside {
    background-color: var(--red-color);
    width: 270px;
    height: 272px;
    padding: 30px;
  }

  &__name {
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    color: var(--light-color);
    margin-bottom: 24px;
  }

  &__button {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: var(--dark-color);
    padding: 12px 24px;
    margin-bottom: 16px;

    &:hover {
      background-color: var(--light-color);
      color: var(--dark-color)
    }
  }

  &__mail {
    @include flex-v-center;
    column-gap: 10px;
  }

  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--light-color);

  }


}








