.table {
  background-color: var(--grey-color);

  &__items {
    margin-bottom: 30px;
  }

  &__item {
    display: flex;
    column-gap: 30px;
    background-color: var(--light-color);
    padding: 27px 30px;
    border-bottom: 1px solid var(--grey-border-color);
  }

  &__reverse {
    background-color: var(--text-color);

    .table__title {
      color: var(--light-color);
      font-weight: 600;
      font-size: 18px;
      line-height: 26px;
    }

    .table__count {
      color: var(--light-color);
      font-weight: 600;
      font-size: 18px;
      line-height: 26px;

    }

    .table__units {
      color: var(--light-color);
      font-weight: 600;
      font-size: 18px;
      line-height: 26px;

    }
  }

  &__title {
    width: 510px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__count {
    width: 270px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__units {
    width: 270px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__button {
    margin: 0 auto;
    display: table;
    padding: 12px 24px;
    font-size: 16px;
    line-height: 24px;
  }
}
