.head_container {
  .nav {
    &__list {
      @include flex-v-center;
    }

    &__item {
      &:not(:last-child) {
        margin-right: 40px;
      }
    }

    &__link {
      font-weight: 500;
      font-size: 18px;
      line-height: 26px;
      color: var(--light-color);

      &:hover {
        color: var(--red-color);
      }
    }

  }
}

.footer .nav__link {
  color: var(--light-color);
