.news_block {
  .block_preview {
    margin-bottom: 100px;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__items {
    @include grid-4-fr;
  }

  &__item {
    min-height: 422px;
    overflow: hidden;
  }

  &__image_preview {
    margin-bottom: 16px;
  }

  &__date {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--grey-date-color);
    margin-bottom: 8px;
  }

  &__name {
    font-weight: 500;
    color: var(--dark-color);
    margin-bottom: 16px;
    &:hover {
      color: var(--red-color);
    }
  }

  &__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
  }
}
