.form1 {
  padding: 54px 70px 70px 70px;
  max-width: 600px;
  margin: 0;
  border-radius: 0px;

  &__title {
    margin-bottom: 32px;
  }

  &__label {
    display: block;
    @include mb(16px);

  }

  &__input {
    padding: 12px 20px;
    background-color: var(--light-color);
    border: 1px solid var(--grey-doc-color);
    border-radius: 4px;
    outline: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
    width: 100%;

    &[type="text"] {
      color: var(--dark-color);
    }
  }

  &__textarea {
    height: 160px;
  }

  &__label_flex {
    @include flex-v-center;
    column-gap: 16px;

    button {
      max-width: 178px;
      padding-left: 50px;
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      color: var(--red-color);
      position: relative;
      cursor: pointer;

      &:before {
        position: absolute;
        content: '';
        left: 20px;
        top: 50%;
        width: 14px;
        height: 14px;
        transform: translateY(-50%);
        background-image: url(../img/svg/plus.svg);
      }
    }

    span {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: #999999;
    }
  }

  &__check {
    display: flex;
    column-gap: 16px;
    margin-bottom: 32px;

    img {
      width: 20px;
      height: 20px;
      background-color: var(--red-color);
      border-radius: 4px;
      padding: 6px 5px;
    }
  }

  &__agree {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #666666;

    a {
      color: var(--red-color);
      text-decoration: underline;
    }
  }

  &__button {
    padding: 12px 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    background-color: var(--red-color);
    color: var(--light-color);
    border-radius: 4px;

    &:hover {
      background-color: var(--red-h-color);
    }
  }

  .graph-modal__close {
    right: 16px;
    top: 16px;
    width: 13px;
    height: 13px;
  }
}
