.article {
  background: linear-gradient(0deg, #F0F0F0, #F0F0F0), #FFFFFF;

  &__container {
    display: flex;
    justify-content: space-between;

  }

  .breadcrumbs {
    margin-bottom: 46px;
    p {
      color: var(--text-color);

      &:last-child {
        color: var(--grey-date-color);
      }
    }
  }

  &__content {
    background-color: var(--light-color);
    position: relative;
    max-width: 900px;
    padding-right: 60px;
    padding-bottom: 100px;
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);

    &:before {
      position: absolute;
      content: '';
      background-color: var(--light-color);
      left: -300%;
      top: 0;
      width: 300%;
      height: 100%;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    margin-bottom: 32px;
    max-width: 616px;
  }

  &__date {
    @include flex-v-center;
    column-gap: 20px;
    margin-bottom: 32px;

    span {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--grey-date-color);
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--dark-color);
    margin-bottom: 80px;
  }

  &__image {
    margin-bottom: 80px;
  }

  &__paragrah {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
    margin-bottom: 80px;

    p {
      @include mb(16px)
    }
  }

  &__video {
    margin-bottom: 80px;
    display: block;
  }

  .plus1 {
    margin-bottom: 80px;
  }

  .plus1__title {
    margin-bottom: 30px;
  }

  .plus5__container {
    margin-bottom: 80px;
  }

  .plus5__text {
    max-width: 440px;

    p {
      font-size: 16px;
      line-height: 24px
    }

  }

  &__path {
    border-top: 1px solid #E6E6E6;
    padding-top: 30px;
    @include flex-all-sb;
  }

  .article__prev {
    @include flex-all-sb;
    column-gap: 20px;
  }

  .article__next {
    @include flex-all-sb;
    column-gap: 20px;
  }

  &__name {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: var(--text-color);
  }

  &__subname {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    color: var(--red-color);
  }

  &__aside {
    width: 240px;
    height: 388px;

    background-color: var(--light-color);
    padding: 20px;
    margin-top: 70px;
  }

  &__nominal {
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: var(--dark-color);
    margin-bottom: 20px;
  }

  &__link {
    @include flex-v-center;
    column-gap: 20px;
    @include mb(16px);
  }

  &__sublink {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-decoration-line: underline;
    color: var(--text-color);
  }
}
