.contact {
  background-color: var(--grey-bg-color);

  .breadcrumbs {
    p {
      color: var(--text-color);

      &:last-child {
        color: var(--grey-date-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__block {
    background-color: var(--light-color);
    box-shadow: 0px 4px 20px rgba(35, 31, 32, 0.15);
    border-radius: 4px;
    padding: 30px;
  }

  &__name {
    margin-bottom: 24px;
  }

  &__infos {
    margin-bottom: 30px;
  }

  &__info {
    @include flex-v-center;
    @include mb(16px);
    column-gap: 8px;
  }

  &__common {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);

    span {
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      margin-right: 8px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--red-color);
  }

  &__mail {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--red-color);
  }

  &__buttons {
    display: flex;
    column-gap: 30px;
  }

  &__button {
    padding: 14px 24px;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    column-gap: 30px;
    margin-top: 30px;
    margin-bottom: 100px;
  }

  &__all {
    min-width: 570px;
  }
}
