.contact_us {
  padding-top: 83px;
  padding-bottom: 83px;
  background-color: var(--red-color);
  border-radius: 4px;
  position: relative;

  &__image {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);

  }

  &__container {
    @include flex-v-center;
    column-gap: 97.5px;

  }

  &__title {
    color: var(--light-color);
  }

  &__button {

    color: var(--red-color);
    white-space: nowrap;
    padding: 19px 32px;
    padding-right: 64px;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      background-image: url(../img/svg/arrow_right_red.svg);
      width: 16px;
      height: 12px;
      right: 36px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
    }

    &:hover {
      background-color: var(--light-color);
      color: var(--red-h-color);
    }
  }

  &__mail {
    @include flex-v-center;
    column-gap: 10px;
  }

  &__mail-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
    white-space: nowrap;
  }
}
