.hero {
  height: 900px;
  position: relative;

  &__slide {
    @include bg-cover;
    padding-top: 296px;
    padding-bottom: 220px;
  }
  &__content {
    position: relative;
    left: 26px;
  }

  &__title {
    font-weight: 500;
    font-size: 72px;
    line-height: 88px;
    color: var(--light-color);
    max-width: 668px;
    margin: 0;
    margin-bottom: 56px;
  }

  &__button {
    position: relative;

    &:before {
      content: "";
      position: absolute;
      background-image: url(../img/svg/arrow_right_white.svg);
      width: 16px;
      height: 12px;
      right: 52px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
    }
  }
}

