@media screen and (max-width: 576px) {
  .form1 {
    padding: 42px 18px 62px 18px;
  }

  .form1__title {
    margin-bottom: 16px;
  }

  .form1__input {
    padding: 14px 16px;
    font-size: 14px;
    line-height: 20px;
  }

  .form1__label:not(:last-child) {
    margin-bottom: 8px;
  }

  .form1__label_flex {
    flex-direction: column;
    align-items: start;
    row-gap: 2px;
  }

  .form1__input {
    padding: 16px;
  }

  .form1__check {
    margin-bottom: 16px;
  }
}

@media screen and (max-width: 375px) {
  .form1__button {
    width: 100%;
  }

}
